<template>
    <div class="novel-link">
        <el-form ref="form" :model="form" label-width="80px" size="mini">
            <el-form-item label="id">
                <el-input v-model="form.id" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="页面标题">
                <el-input v-model="form.title" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="文章标题">
                <el-input v-model="form.headline" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号名称">
                <el-input v-model="form.gonghao" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="公号描述">
                <el-input v-model="form.author" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="图片" prop="imageFile">
                <el-upload :show-file-list="false" enctype="multipart/form-data" name="imageFile" action="http://crm.rrdtjj.top/message/upload" :on-success="handleAvatarSuccess">
                    <el-button size="small" type="primary">点击上传</el-button>
                    <img v-if="imgUrl" :src="imgUrl" alt="" width="200">
                </el-upload>
            </el-form-item>
            <el-form-item label="文章内容" prop="content">
                <el-input type="textarea" v-model="form.content" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="跳转链接">
                <el-input v-model="form.linkUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item label="统计链接">
                <el-input v-model="form.statisticsUrl" style="width:500px;"></el-input>
            </el-form-item>
            <el-form-item size="large">
                <el-button type="primary" @click="onCreate">立即创建</el-button>
                <el-button type="danger" @click="reset">重置</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                
            };
        },
        mounted() {

        },
        methods: {
        }
    };
</script>

